import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { useLocation } from '@reach/router';
import { Helmet } from 'react-helmet';
import image from '../images/social.png';

const SEO = ({ title: customTitle, description: customDescription }) => {
  const {
    site: {
      siteMetadata: {
        siteUrl,
        title: defaultTitle,
        description: defaultDescription,
      },
    },
  } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            siteUrl
            title
            description
          }
        }
      }
    `
  );

  const { pathname } = useLocation();

  const url = `${siteUrl}${pathname}`;
  const title =
    pathname === '/'
      ? `${defaultTitle} | Software Developer/Engineer`
      : `${customTitle} | ${defaultTitle}`;
  const description = customDescription || defaultDescription;

  return (
    <Helmet>
      <html lang="en" />

      <title>{title} </title>
      <meta name="description" content={description} />
      <meta name="image" content={image} />

      <meta property="og:locale" content="en_US" />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={url} />
      <meta property="og:site_name" content="Safdar Jamal" />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={image} />

      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:creator" content="@_safdarjamal" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
    </Helmet>
  );
};

export default SEO;
